    filter: brightness(1.5) drop-shadow(0 0 8px currentColor);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1) rotate(180deg);
    filter: brightness(1.2) drop-shadow(0 0 4px currentColor);
  }
  75% {
